.title {
    margin-top: 3%;
    color: $white;

    input {
        width: 30%;
        text-align: center;
        margin-top: 25px;
        margin-bottom: 25px;
        padding: 5px;
        border-radius: 5px;
        border: 1px solid $color-1;
        box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
    }

    .btn-container {
        display: flex;
        justify-content: center;
        align-items: center;

        button {
            width: 70px;
            height: 20px;
            font-size: 0.7rem;
            margin: 0 2px;
            cursor: pointer;

            &:hover {
                filter: brightness(110%);
            }
        }
    }
}