.card {
    background-color: $white;
    display: inline-flex;
    position: relative;
    margin-top: 3%;
    height: max-content;
    width: 350px;
    border-radius: 30px;
    transition: 0.3s;
    margin-left: 15px;

    img {
        height: 100px;
        width: 160px;
        object-fit: cover;
        border-radius: 10px;
        margin-top: 15px;
    }

    h1 {
        font-size: medium;
        color: $color-3;
        padding-top: 25px;
    }

    h3 {
        color: $color-1;
        padding-top: 15px;
    }

    p {
        padding: 5%;
        margin-bottom: 20px;
        color: $color-3;
        font-size: medium;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 8;
        overflow: hidden;
    }

}

.card:hover {
    border: 1px solid red;
}